.whatud__possibility{
    display: flex;
    flex-direction: row;
}

.whatud__possibility-image{
    flex: 1;
    display: flex;
    justify-content: flex-start;
    align-items: center;
}

.whatud__possibility-image img{
    width: 100%;
    height: 100%;
}

.whatud__possibility-content{
    flex: 1;
    display: flex;
    justify-content: flex-end;
    flex-direction: column;
    
}


.whatud__possibility-content h4{
    font-family: var(--font-family);
    font-weight: 500;
    font-size: 16px;
    line-height: 30px;
    color: #71E5FF;
}

.whatud__possibility-content h4:last-child{
    color: #FF8A71;
}

.whatud__possibility-content h1{
    font-family: var(--font-family);
    font-weight: 800;
    font-size: 34px;
    line-height: 45px;
    margin: 1rem 0;
}


.whatud__possibility-content p{
    font-family: var(--font-family);
    
    font-size: 16px;
    line-height: 30px;
    color: var(--color-text);
    margin-bottom: 2rem;
}

@media screen and (max-width: 950px) {
    .whatud__possibility{
        flex-direction: column;
    }

    .whatud__possibility-image{
        margin: 1rem 0;
    }

    .whatud__possibility-content{
        margin-top: 2rem;
    }
}